import React from 'react';
import DonatusImg from './DonatusImg.jpg'
import './About.css'
const About = () => {
	return(
		<div className='wrap'>
			<div className='contain'>
				<h1>ABOUT ME<br /></h1>
			</div>
			<div className='ipsem'>
				<div className='ipsem-img'>
					<img className='about-Img'  src={DonatusImg} alt="Donatus Silva Richard"/>
				</div>
				<div className='ipsem-p f4'>
				<p >I am a self-taught software developer.
				 I am passionate about developing innovative
				 and intuitive applications. I would like to work
				 with a team of interesting individuals who care about
				 building creative applications so I can learn from them.
				 I would like to use my skills and experiences
				 to build a successful career in software development. <br />


				  </p>
				</div>
			</div>
				
		</div>

		);

}

export default About;